.SnackbarContentWrapperStyles {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.SnackbarContentStyles {
    position: relative;
    word-wrap: break-word;
    width: 100%;
    height: 100%;
    padding: 15px 25px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}